import 'vux/src/styles/reset.less';
import './buy_member.scss'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'


Vue.use(ToastPlugin)

import { hostImg } from 'js/host-config'

import { fetch, rap } from 'js/fetch.js'
let url = {
getVip: 'api/vips',

}
url = rap(url)
import Myhead from 'components/head/head.vue'

import Top from 'components/top/top.vue'

import Foot from 'components/foot/foot.vue'

import mixin from 'js/mixin.js'

new Vue({
el: '#body',
data: {
vipList: [],
listIndex: 0,
transitionName: 'slide-left'
},
created() {
this.getVip()
},
methods: {
getVip() {

fetch('get', url.getVip).then(res => {
if (res.status >= 200 && res.status <= 300) {
console.log(res.data)




















res.data.forEach(element => {
let obj = {
id: element.id,
name: element.name,
icon: `${hostImg}${element.icon}`,
price: element.price,
type: element.type,
}
this.vipList.push(obj)
});

} else {
this.$vux.toast.show({
text: res.data.message,
type: 'warn',
onShow() {

},
onHide() {

}
})
}
})
},
show(index) {
if (this.listIndex > index) {
this.transitionName = 'slide-right'
} else {
this.transitionName = 'slide-left'
}
this.listIndex = index
}
},
components: {
Cell,
Group,
Swiper,
Scroller,
Tab,
TabItem,
Sticky,
Flexbox,
FlexboxItem,
ViewBox,
XButton
},
mixins: [mixin]
})



